@charset "UTF-8";
/**
 * 变量库
 */
:global {
  /**
     * 组件
     */
  /**
     * 基本样式
     */
  /**
 * 不写类名或ID名不受css modules影响
 * 基本样式
 */
  /*
 * a 标签基本样式
 */ }
  :global .header {
    font-size: 2rem;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    margin-bottom: 15px; }
  :global * {
    margin: 0;
    padding: 0; }
  :global a {
    cursor: pointer;
    text-decoration: none; }
    :global a:link {
      color: black; }
    :global a:active {
      color: red; }
    :global a:visited {
      color: purple; }
    :global a:hover {
      color: blue; }



